body {
  margin: 0;
  font-family: 'Oxygen', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:root {
    --primary: #2962ff;
    --primary-lighter: #81d4fa;
    --primary-lightest: #e3f2fd;
    --secondary: #FF4081;
}

.App {
    text-align: center;
    background: white;
    display: flex;
    flex-direction: row;
    width: 100vw;
    height: 100vh;
}

::-webkit-scrollbar {
    width: .3em;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background: #888;
}

::-webkit-scrollbar-thumb:hover {
    background: #555;
}

div.list {
    display: flex;
    flex-direction: column;
    overflow: auto;
    max-height: 50vh;
}

input[type=text], select {
    box-sizing: border-box;
    border: 1px solid #777;
    width: 100%;
    font-family: inherit;
    font-size: smaller;
    font-weight: bold;
    background: white;
    border-radius: .1em;
    margin: .3em;
    padding: .5em;
}

input[type=text]:focus, select:focus {
    outline: none;
}

input[type=text]:focus {
    border-bottom: .1em solid black;
}

input[type=text]::selection {
    background: #81d4fa;
    background: var(--primary-lighter);
}

input[type=text]::-moz-selection {
    background: #81d4fa;
    background: var(--primary-lighter);
}

input[type=range] {
    /* -webkit-appearance: none; */
    width: 100%;
}

.action {
    background: transparent;
    transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
    cursor: pointer;
}

.action:hover {
    color: #FF4081;
    color: var(--secondary);
}

.action:active {
    transform: scale(.8);
}
svg {
    height: 100%;
    overflow: hidden;
}

.link {
    stroke: #FF4081;
}

.arrow {
    fill: #FF4081;
}

.node text {
    stroke: black;
    cursor: pointer;
}

.node circle {
    fill: #FF4081;
    cursor: pointer;
    transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.node circle:hover {
    transform: scale(2, 2);
}

.node circle:active {
    transform: scale(1.8, 1.8);
}
div.node {
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
div.link {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
div.link {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

div.link-value {
    box-sizing: border-box;
    width: 100%;
    font-family: inherit;
    font-size: smaller;
    font-weight: bold;
    margin: .3em;
    padding: .5em;
    overflow: hidden;
}
.algorithm {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    overflow: hidden;
    white-space: nowrap;
}

.algorithm>select {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
div.floating-panel {
    position: fixed;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background: var(--primary-lightest);
    height: auto;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.2);
    border-radius: .3em;
    transition: all 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
}

div.floating-panel-min {
    width: 10em;
}

div.floating-panel-max {
    width: 20em;
}

div.floating-panel-title {
    background: var(--primary);
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.3);
    border-radius: .3em .3em 0 0;
    margin-bottom: 3px;
    color: white;
    padding: 1em;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    cursor: move;
}

div.floating-panel-content {
    padding: 1em;
}

div.floating-panel-expand {
    width: 1em;
}
